<script setup>
import { ref, watchEffect, defineModel, watch, defineExpose, computed, defineEmits, nextTick } from "vue";
import { VueMonacoEditor, VueMonacoDiffEditor, loader } from "@guolao/vue-monaco-editor";
import { setupColorScheme, useColorScheme } from "@/assets/js/functions/useColorScheme.js";

import "./src/editor.main.css";
import { setupMonacoFormatters } from "./src/setupMonacoFormatters.js";
// Import the new autocomplete setup functions
//import { setupMonacoAutocomplete, getAutocompleteEditorOptions } from "./src/setupMonacoAutocomplete.js";
import { registerPhpAutocomplete, applyPhpEditorOptions } from "./src/phpAutocomplete.js";
import { registerWordPressAutocomplete } from "./src/WordPressAutocomplete.js";
//import "./src/overrides.css";

// Store
import { useAppStore } from "@/store/app/app.js";
const appStore = useAppStore();

import { unsavedChanges } from "@/setup/constants.js";
const { prefersDark } = useColorScheme();
const model = defineModel();
const modifiedCode = ref("");
const emits = defineEmits(["updated"]);
const isDiffMode = ref(false);
const monacoEditorInstance = ref(null);
const customTheme = ref("vs");
const props = defineProps({
  language: String,
  ensurePHP: { type: Boolean, default: true },
  enableAutocomplete: { type: Boolean, default: true },
});

const editorContainer = ref(null);

// Map Ace language modes to Monaco language IDs
const getMonacoLanguage = computed(() => {
  const languageMap = {
    JAVASCRIPT: "javascript",
    CSS: "css",
    HTML: "html",
    PHP: "php",
    SCSS: "scss",
  };
  return languageMap[props.language] || props.language;
});

// Base editor options
const baseEditorOptions = {
  tabSize: 3,
  wordWrap: "on",
  fontSize: appStore.state.snipvault_settings.code_font_size || 12,
  minimap: {
    enabled: false,
  },
  lineNumbers: "on",
  lineDecorationsWidth: 24,
  glyphMargin: false,
  folding: false,
  scrollBeyondLastLine: false,
  fixedOverflowWidgets: true,
  theme: prefersDark.value ? "vs-dark" : customTheme.value,
  semanticHighlighting: {
    enabled: true,
  },
  padding: {
    top: 16,
    bottom: 16,
  },
  overviewRulerLanes: 0,
  hideCursorInOverviewRuler: true,
  overviewRulerBorder: false,
  documentFormattingEdits: false,
  scrollbar: {
    useShadows: false,
    vertical: "visible",
    horizontal: "visible",
    verticalScrollbarSize: 10,
    horizontalScrollbarSize: 10,
  },
  renderLineHighlight: "none",
  automaticLayout: true,
  quickSuggestions: true,
  suggestOnTriggerCharacters: true,
  acceptSuggestionOnEnter: "on",
  tabCompletion: "on",
  wordBasedSuggestions: true,
  parameterHints: { enabled: true },
};

// Get editor options with autocomplete settings if enabled
//const editorOptions = ref(props.enableAutocomplete ? getAutocompleteEditorOptions(baseEditorOptions) : baseEditorOptions);

const editorOptions = ref(baseEditorOptions);

let currentEditor = null;

const handleEditorChange = (value) => {
  emits("updated");
  model.value = value;
};

const handleEditorMount = (editor, instance) => {
  currentEditor = editor;

  editor.focus();

  // Listen for content changes in diff view only
  if (isDiffMode.value) {
    editor.onDidUpdateDiff((whatIsThis) => {
      const newValue = editor.getModifiedEditor().getValue();
      model.value = newValue;
    });
  }

  monacoEditorInstance.value = instance.editor;

  setupMonacoFormatters(instance);

  if (getMonacoLanguage.value === "php") {
    registerPhpAutocomplete(monaco);
    registerWordPressAutocomplete(monaco);
    applyPhpEditorOptions(monaco, editor);
  }

  monacoEditorInstance.value.defineTheme("snipvault-light", {
    base: "vs", // can also be vs or hc-black
    inherit: true,
    rules: [],
    colors: {
      "editor.background": "#fafafa", // code background
    },
  });

  customTheme.value = "snipvault-light";

  // Force a layout update after mounting
  setTimeout(() => {
    editor.layout();
  }, 100);
};

const updateContent = (content) => {
  model.value = content;
};

const setDiffMode = (code) => {
  modifiedCode.value = code;
  isDiffMode.value = true;
};

const formatJsonDocument = () => {
  if (!currentEditor) return;

  try {
    // Get current value
    const currentValue = currentEditor.getValue();

    // Parse it to ensure it's valid JSON
    const parsed = JSON.parse(currentValue);

    // Stringify with indentation (the '2' adds 2 spaces per level)
    const formatted = JSON.stringify(parsed, null, 2);

    // Set the formatted value back to editor
    currentEditor.setValue(formatted);
  } catch (e) {
    console.error("Failed to format JSON:", e);
  }
};

const formatDocument = async () => {
  if (!currentEditor) return;
  if (!currentEditor.getAction) return;

  await currentEditor.getAction("editor.action.formatDocument").run();
};

const exitDiffMode = () => {
  model.value = currentEditor.getModifiedEditor().getValue();

  modifiedCode.value = "";
  isDiffMode.value = false;
};

const updateLayout = () => {
  currentEditor.layout();
};

// Watch for theme changes
watch(
  () => prefersDark.value,
  (newValue) => {
    editorOptions.value.theme = newValue ? "vs-dark" : customTheme.value;
  }
);

// Watch for language changes to reconfigure language services
watch(
  () => getMonacoLanguage.value,
  (newLanguage) => {
    if (monacoEditorInstance.value && props.enableAutocomplete) {
      // Create a proper instance object for the autocomplete function
      const instance = {
        monaco: loader.monaco,
        editor: monacoEditorInstance.value,
      };
      //setupMonacoAutocomplete(instance, newLanguage);
    }
  }
);

// Watch for autocomplete setting change
watch(
  () => props.enableAutocomplete,
  (enableAutocomplete) => {
    // Update editor options
    editorOptions.value = enableAutocomplete ? getAutocompleteEditorOptions(baseEditorOptions) : baseEditorOptions;

    // Apply the configuration if editor is already mounted
    if (enableAutocomplete && monacoEditorInstance.value) {
      // Create a proper instance object for the autocomplete function
      const instance = {
        monaco: loader.monaco,
        editor: monacoEditorInstance.value,
      };
      //setupMonacoAutocomplete(instance, getMonacoLanguage.value);
    }
  }
);

watch(
  () => props.language,
  async () => {
    if (!props.ensurePHP) return;

    const phpHeader = "<?php\n// Prevent direct access to this file\ndefined('ABSPATH') || exit();";
    const phpHeaderAlt = `<?php\n// Prevent direct access to this file\ndefined("ABSPATH") || exit();`;

    // Normalize the content first
    let content = model.value?.trim() || "";

    if (props.language === "PHP") {
      // Remove any existing PHP tags and headers
      if (content.startsWith("<?php")) {
        // Remove just the <?php tag
        content = content.replace(/^<\?php\s*/i, "");
      }

      // Remove any existing header patterns if they exist
      content = content.replace(/^\/\/\s*Prevent direct access[\s\S]*?exit\(\);/i, "");

      // Add the header
      model.value = `${phpHeaderAlt}\n\n${content.trim()}`;
    } else {
      // For non-PHP, remove the PHP headers if they exist
      if (content.startsWith(phpHeader)) {
        content = content.replace(phpHeader, "").trim();
      } else if (content.startsWith(phpHeaderAlt)) {
        content = content.replace(phpHeaderAlt, "").trim();
      }

      model.value = content;

      nextTick(() => {
        unsavedChanges.value = false;
      });
    }
  },
  { immediate: true }
);

defineExpose({ updateContent, setDiffMode, exitDiffMode, updateLayout, formatJsonDocument, formatDocument });
</script>

<template>
  <VueMonacoDiffEditor
    v-if="isDiffMode"
    :original="modifiedCode"
    :modified="model"
    :language="getMonacoLanguage"
    :options="editorOptions"
    :diffOptions="{ renderSideBySide: true, originalEditable: false }"
    @change="handleEditorChange"
    @mount="handleEditorMount"
    width="auto"
    class="snipvault-editor"
  />
  <VueMonacoEditor v-else v-model:value="model" :language="getMonacoLanguage" :options="editorOptions" @change="handleEditorChange" @mount="handleEditorMount" width="auto" class="snipvault-editor" />
</template>
