// A streamlined autocomplete provider for PHP in Monaco Editor

/**
 * Register PHP autocomplete features for Monaco Editor
 * @param {Object} monaco - The Monaco Editor instance
 */
export function registerPhpAutocomplete(monaco) {
  if (!monaco || !monaco.languages) {
    console.error("Monaco languages API not available");
    return;
  }

  // Make sure PHP language is registered
  if (!monaco.languages.getLanguages().some((lang) => lang.id === "php")) {
    monaco.languages.register({ id: "php" });
  }

  // Register PHP autocompletion provider
  monaco.languages.registerCompletionItemProvider("php", {
    triggerCharacters: ["$", ">", ":"],

    provideCompletionItems: (model, position) => {
      const textUntilPosition = model.getValueInRange({
        startLineNumber: position.lineNumber,
        startColumn: 1,
        endLineNumber: position.lineNumber,
        endColumn: position.column,
      });

      // Determine context to provide appropriate suggestions
      let suggestions = [];

      // Object method suggestions after ->
      if (textUntilPosition.match(/\w+\s*->\s*$/)) {
        suggestions = [...getPhpMethods(monaco)];
      }
      // Static method or constant suggestions after ::
      else if (textUntilPosition.match(/\w+\s*::\s*$/)) {
        suggestions = [...getPhpStaticMethods(monaco)];
      }
      // Variable suggestions after $
      else if (textUntilPosition.endsWith("$")) {
        suggestions = [...getPhpVariables(monaco)];
      }
      // General PHP suggestions
      else {
        suggestions = [...getPhpKeywords(monaco), ...getPhpFunctions(monaco), ...getPhpSnippets(monaco)];
      }

      return {
        suggestions: suggestions,
      };
    },
  });
}

/**
 * Generate PHP keywords completion items
 * @param {Object} monaco - The Monaco Editor instance
 * @returns {Array} An array of completion items
 */
function getPhpKeywords(monaco) {
  const keywords = [
    "abstract",
    "and",
    "array",
    "as",
    "break",
    "callable",
    "case",
    "catch",
    "class",
    "clone",
    "const",
    "continue",
    "declare",
    "default",
    "die",
    "do",
    "echo",
    "else",
    "elseif",
    "empty",
    "enddeclare",
    "endfor",
    "endforeach",
    "endif",
    "endswitch",
    "endwhile",
    "eval",
    "exit",
    "extends",
    "final",
    "finally",
    "for",
    "foreach",
    "function",
    "global",
    "goto",
    "if",
    "implements",
    "include",
    "include_once",
    "instanceof",
    "insteadof",
    "interface",
    "isset",
    "list",
    "namespace",
    "new",
    "or",
    "print",
    "private",
    "protected",
    "public",
    "require",
    "require_once",
    "return",
    "static",
    "switch",
    "throw",
    "trait",
    "try",
    "unset",
    "use",
    "var",
    "while",
    "xor",
    "yield",
    "yield from",
  ];

  return keywords.map((keyword) => ({
    label: keyword,
    kind: monaco.languages.CompletionItemKind.Keyword,
    insertText: keyword,
    detail: "PHP Keyword",
  }));
}

