/**
 * Generate PHP snippets completion items
 * @param {Object} monaco - The Monaco Editor instance
 * @returns {Array} An array of completion items
 */
function getPhpSnippets(monaco) {
  const snippets = [
    {
      label: "php",
      detail: "PHP block",
      insertText: "<?php\n\t${0}\n?>",
    },
    {
      label: "class",
      detail: "Class definition",
      insertText: "class ${1:ClassName} {\n\t${0}\n}",
    },
    {
      label: "function",
      detail: "Function definition",
      insertText: "function ${1:functionName}(${2:$param}) {\n\t${0}\n}",
    },
    {
      label: "if",
      detail: "If statement",
      insertText: "if (${1:condition}) {\n\t${0}\n}",
    },
    {
      label: "ifelse",
      detail: "If else statement",
      insertText: "if (${1:condition}) {\n\t${2}\n} else {\n\t${0}\n}",
    },
    {
      label: "foreach",
      detail: "Foreach loop",
      insertText: "foreach (${1:$array} as ${2:$value}) {\n\t${0}\n}",
    },
    {
      label: "foreachkey",
      detail: "Foreach loop with key",
      insertText: "foreach (${1:$array} as ${2:$key} => ${3:$value}) {\n\t${0}\n}",
    },
    {
      label: "for",
      detail: "For loop",
      insertText: "for (${1:$i} = ${2:0}; ${1:$i} < ${3:count}; ${1:$i}++) {\n\t${0}\n}",
    },
    {
      label: "while",
      detail: "While loop",
      insertText: "while (${1:condition}) {\n\t${0}\n}",
    },
    {
      label: "try",
      detail: "Try catch block",
      insertText: "try {\n\t${1}\n} catch (${2:Exception} ${3:$e}) {\n\t${0}\n}",
    },
    {
      label: "namespace",
      detail: "Namespace declaration",
      insertText: "namespace ${1:Namespace};\n\n${0}",
    },
    {
      label: "use",
      detail: "Use statement",
      insertText: "use ${1:Namespace}\\${2:Class};\n\n${0}",
    },
  ];

  return snippets.map((snippet) => ({
    label: snippet.label,
    kind: monaco.languages.CompletionItemKind.Snippet,
    detail: snippet.detail,
    insertText: snippet.insertText,
    insertTextRules: monaco.languages.CompletionItemInsertTextRule.InsertAsSnippet,
  }));
}

