/**
 * Apply PHP editor options to enhance the editing experience
 * @param {Object} monaco - The Monaco Editor instance
 * @param {Object} editor - The editor instance
 */
export function applyPhpEditorOptions(monaco, editor) {
  if (!editor) return;

  // Set editor options for better PHP editing
  editor.updateOptions({
    tabSize: 4,
    insertSpaces: true,
    autoIndent: "full",
    autoClosingBrackets: "always",
    autoClosingQuotes: "always",
    autoSurround: "languageDefined",
    formatOnPaste: true,
    formatOnType: true,
    wordWrap: "off",
    quickSuggestions: {
      other: true,
      comments: true,
      strings: true,
    },
    suggestOnTriggerCharacters: true,
    acceptSuggestionOnEnter: "on",
    tabCompletion: "on",
    wordBasedSuggestions: true,
    parameterHints: { enabled: true },
  });
}
