/**
 * WordPress-specific autocomplete provider for Monaco Editor
 * This module provides WordPress functions, hooks, filters, and common patterns
 */

/**
 * Register WordPress autocomplete features for Monaco Editor
 * @param {Object} monaco - The Monaco Editor instance
 */
export function registerWordPressAutocomplete(monaco) {
  if (!monaco || !monaco.languages) {
    console.error("Monaco languages API not available");
    return;
  }

  // Register WordPress autocompletion provider (extends PHP provider)
  monaco.languages.registerCompletionItemProvider("php", {
    triggerCharacters: ["$", ">", ":", "_"],

    provideCompletionItems: (model, position) => {
      const textUntilPosition = model.getValueInRange({
        startLineNumber: position.lineNumber,
        startColumn: 1,
        endLineNumber: position.lineNumber,
        endColumn: position.column,
      });

      // Determine context to provide appropriate suggestions
      let suggestions = [];

      // Add suggestions for WordPress hooks when the text contains "add_"
      if (textUntilPosition.match(/add_a/i)) {
        suggestions = [...getWordPressActionHooks(monaco)];
      }
      // Add suggestions for WordPress filters when the text contains "apply_"
      else if (textUntilPosition.match(/apply_f/i)) {
        suggestions = [...getWordPressFilterHooks(monaco)];
      }
      // WordPress global variables
      else if (textUntilPosition.endsWith("$")) {
        suggestions = [...getWordPressGlobals(monaco)];
      }
      // WordPress common functions
      else {
        suggestions = [...getWordPressFunctions(monaco), ...getWordPressHooks(monaco)];
      }

      return {
        suggestions: suggestions,
      };
    },
  });
}

/**
 * Generate WordPress functions completion items
 * @param {Object} monaco - The Monaco Editor instance
 * @returns {Array} An array of completion items
 */
