/**
 * Generate WordPress global variables suggestions
 * @param {Object} monaco - The Monaco Editor instance
 * @returns {Array} An array of completion items
 */
function getWordPressGlobals(monaco) {
  const globals = [
    { label: "$post", detail: "Current post object" },
    { label: "$wp_query", detail: "Global WP_Query instance" },
    { label: "$wpdb", detail: "WordPress database access abstraction class" },
    { label: "$wp_rewrite", detail: "WordPress rewrite component" },
    { label: "$wp_locale", detail: "WordPress date and time locale object" },
    { label: "$wp_styles", detail: "WordPress styles enqueue handler" },
    { label: "$wp_scripts", detail: "WordPress scripts enqueue handler" },
    { label: "$wp_roles", detail: "WordPress user roles" },
    { label: "$current_user", detail: "Current user data (use wp_get_current_user() instead)" },
    { label: "$wp_version", detail: "WordPress version string" },
    { label: "$is_IE", detail: "Whether the user agent is Internet Explorer" },
    { label: "$is_edge", detail: "Whether the user agent is Edge browser" },
    { label: "$is_chrome", detail: "Whether the user agent is Chrome" },
    { label: "$is_safari", detail: "Whether the user agent is Safari" },
    { label: "$is_gecko", detail: "Whether the user agent is Gecko based" },
    { label: "$is_opera", detail: "Whether the user agent is Opera" },
    { label: "$is_iphone", detail: "Whether the user agent is iPhone" },
    { label: "$is_mobile", detail: "Whether the user agent is mobile" },
  ];

  return globals.map((variable) => ({
    label: variable.label,
    kind: monaco.languages.CompletionItemKind.Variable,
    detail: variable.detail,
    insertText: variable.label.substring(1), // Remove $ since we're triggering on $
  }));
}
