// Import Prettier and its plugins
import prettier from "prettier/standalone";
import phpParser from "@prettier/plugin-php/standalone";
import babelParser from "prettier/parser-babel";
import htmlParser from "prettier/parser-html";
import cssParser from "prettier/parser-postcss";
import * as prettierPluginEstree from "prettier/plugins/estree";

// Configure Monaco formatters with Prettier
export function setupMonacoFormatters(monaco) {
  const jsFormatter = {
    displayName: "Format with Prettier",
    provideDocumentFormattingEdits: async (model) => {
      const text = model.getValue();
      try {
        const formatted = await prettier.format(text, {
          parser: "babel",
          plugins: [babelParser, prettierPluginEstree],
          tabWidth: 2,
          printWidth: 100,
          estree: prettierPluginEstree,
        });
        return [
          {
            range: model.getFullModelRange(),
            text: formatted,
          },
        ];
      } catch (error) {
        console.error("JS formatting error:", error);
        return [];
      }
    },
  };

  // JavaScript formatter
  monaco.languages.registerDocumentFormattingEditProvider({ language: "javascript", exclusive: true }, jsFormatter);
  monaco.languages.registerDocumentFormattingEditProvider({ language: "typescript", exclusive: true }, jsFormatter);

  // HTML formatter
  monaco.languages.registerDocumentFormattingEditProvider(["html", "xml"], {
    displayName: "Format with Prettier",
    provideDocumentFormattingEdits: async (model) => {
      const text = model.getValue();
      const formatted = await prettier.format(text, {
        parser: "html",
        plugins: [htmlParser],
        tabWidth: 2,
        printWidth: 100,
        htmlWhitespaceSensitivity: "css",
      });

      return [
        {
          range: model.getFullModelRange(),
          text: formatted,
        },
      ];
    },
  });

  // CSS formatter
  monaco.languages.registerDocumentFormattingEditProvider(["css", "scss", "less"], {
    displayName: "Format with Prettier",
    provideDocumentFormattingEdits: async (model) => {
      const text = model.getValue();
      const parserName = model.getLanguageId();

      const formatted = await prettier.format(text, {
        parser: parserName,
        plugins: [cssParser],
        tabWidth: 2,
        printWidth: 100,
      });

      return [
        {
          range: model.getFullModelRange(),
          text: formatted,
        },
      ];
    },
  });

  // PHP formatter
  monaco.languages.registerDocumentFormattingEditProvider("php", {
    displayName: "Format with Prettier",
    provideDocumentFormattingEdits: async (model) => {
      let text = model.getValue();
      text = await prettier.format(text, {
        parser: "php",
        plugins: [phpParser],
        tabWidth: 2,
        printWidth: 100,
      });

      return [
        {
          range: model.getFullModelRange(),
          text: text,
        },
      ];
    },
  });
}
